@import './variables';

*{
    box-sizing: border-box;
}

//Default Reset
a{
    color: inherit;
    text-decoration: none;
}

body{
    font-family: 'Inter', sans-serif;
    margin: 0;
    background-color: $main-bg;
}

.card-container{
    max-width: $card-width;
    background-color: $card-bg;
    margin: 44px auto;
    border-radius: $card-border-radius;
}

.profile-container{
    width: $card-width;
    height: $card-width;
    background-color: $white;
    background-size: cover;
    background-position: center;
    border-radius: $card-border-radius $card-border-radius 0 0;
}

.email-icon::before{
    background-image: url('../images/icons/mail.svg');
}

.linkedin-icon::before{
    background-image: url('../images/icons/linkedin.svg');
}

.email-icon::before, .linkedin-icon::before{
    content: "";
    display: inline-block;
    width: 16px;
    height: 16px;
    margin-right: 8px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.cta{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 115px;
    padding: 10px 0;
    margin: 0 10px;
    font-size: 14px;
    border-radius: 6px;
    border: 0;
    outline: 0;
    border: 1px solid #D1D5DB;
    
    &:active{
        border: 0;
        outline: 0;
    }
}

.bg-blue{
    background-color: #5093E2;
    color: #fff;
    border-color: #5093E2;
}

.cta-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
}

.info-container{
    margin-bottom: 33px;
    text-align: center;
    color: #fff;

    .name{
        font-size: 25px;
        font-weight: bold;
        margin: 0;
        margin-top: 22px;
    }

    .profession{
        margin-top: 6px;
        margin-bottom: 8px;
        color: $brown;
        font-size: 12px;
    }

    .site-link{
        display: block;
        font-size: 10px;
        margin: 0;
        margin-top: 8px;
    }
}

.about-container, .interest-container{
    color: #fff;
    padding-left: 36px;
    padding-right: 34px;
    margin-bottom: 24px;

    h2{
        font-size: 16px;
        font-weight: bold;
        margin: 0;
        margin-bottom: 5px;
        color: #F5F5F5;
    }

    p{
        font-size: 12px;
        margin: 0;
        color: #DCDCDC;
    }
}

.footer{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $dark-bg;
    padding: 20px 0;
    border-radius: 0 0 $card-border-radius $card-border-radius;

    .social-link{
        display: block;
        width: 25px;
        height: 25px;
        margin: 0 12px;

        .social-icon{
            display: block;
            width: 100%;
            height: 100%;
        }
    }
}